.landing-title {
  margin: 3em 0; }
  .landing-title p {
    font-size: 2em;
    width: 48%;
    text-align: center;
    margin: 0 auto;
    color: #FFFFFF; }
    .landing-title p span {
      color: #9BD331; }

.landing-content {
  margin-top: 4rem; }
  .landing-content .left-content {
    background-color: #FFFFFF; }
  .landing-content .right-content .container-network-graph {
    margin-top: 4rem; }

.tooltip-wrapper {
  position: absolute;
  top: -35px;
  left: -50px; }
  .tooltip-wrapper .tooltip {
    width: 100px;
    text-align: center;
    position: relative; }
    .tooltip-wrapper .tooltip .tooltip-content {
      background-color: #405063;
      padding: 0.5rem;
      border-radius: 5px; }
      .tooltip-wrapper .tooltip .tooltip-content .tooltip-content-field {
        display: block; }
      .tooltip-wrapper .tooltip .tooltip-content .tooltip-content-syllabi {
        color: #FFFFFF; }
    .tooltip-wrapper .tooltip .tooltip-tail {
