.header-landing {
  background-image: url("parallax_image.jpg");
  min-height: 27em; }

.header {
  background-image: url("parallax_image.jpg");
  min-height: 17em; }

.main-layout {
  background-color: #F4F5F9;
  min-height: 50em; }

.main-layout section {
  top: -22em;
  position: relative;
  width: 100%; }

.navbar-fixed {
  padding: 2.5em 0 0 0;
  position: fixed;
  width: 100%;
  z-index: 10000;
  background-image: url("parallax_image.jpg");
  -webkit-transition: all 0.4s ease;
    -webkit-transition-transition: all 0.4s ease; }

.shrink {
  padding: 2rem 0; }

@media (max-width: 414px) {
  .header-landing {
    min-height: 20em; }
  .main-layout .huge-container {
    width: 100%;
    padding: 0; }
  .main-layout section {
    top: -19.8em; } }

@media (min-width: 643px) {
  .shrink {
    padding: 1rem 0; } }



